@use "../abstracts/mixins" as *;
@use "../base/colors" as *;

.header {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center ;
	width: 100%;
	// text-align: center;
	height: 100dvh;
	min-height: 600px;
	background-image: url("../../dist/img/header_small.png");
	background-size: cover;
	background-position: center;
	z-index: 1;
	overflow: hidden;
	&::after {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(20, 23, 25, 0.98);
		z-index: -1;
	}
	&__info {
		position: absolute;
		bottom: 3em;
		font-size: clamp(1.6rem, 1.3rem + 1.7143vw, 2.8rem);
		&-link {
			color: #fdb802;
			transition: color 0.3s;
			position: relative;

			&:hover {
				text-decoration: underline;
				color: #fff;
			}
		}
		&-text {
			color: #fff;
            text-align: center;
		}
	}
	&__box {
        &-heading {
			line-height: 1.2;
			color: #edf2ff;
			font-size: clamp(2.2rem, 0.75rem + 8.2857vw, 8rem);
			& span {
				display: inline-block;
				position: relative;
				// background-color: #ffb800;
				// transform: rotate(-3deg);
				width: max-content;
				padding: 0.3em 0.2em;
				z-index: 1;
				&::after {
					position: absolute;
					content: "";
					top: 50%;
					left: 0;
					animation: xd 1s forwards;
					height: 60%;
					transform: translate(0, -50%);
					border-radius: 17px;
					background-color: #fdb802;
					z-index: -1;
				}
			}
		}
		&-text {
			font-size: clamp(1.6rem, 1.3rem + 1.7143vw, 2.8rem);
			color: #dcdcdc;
			font-weight: 200;
		}
	}
}
@keyframes xd {
	from {
		width: 0;
	}
	to {
		width: 100%;
	}
}
