* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden !important;
}

body,
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  font-size: 62.5%;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
  padding: 0em 4em;
}

a {
  text-decoration: none;
  color: #000;
}

ul {
  list-style-type: none;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100dvh;
  min-height: 600px;
  background-image: url("../../dist/img/header_small.png");
  background-size: cover;
  background-position: center;
  z-index: 1;
  overflow: hidden;
}
.header::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 23, 25, 0.98);
  z-index: -1;
}
.header__info {
  position: absolute;
  bottom: 3em;
  font-size: clamp(1.6rem, 1.3rem + 1.7143vw, 2.8rem);
}
.header__info-link {
  color: #fdb802;
  transition: color 0.3s;
  position: relative;
}
.header__info-link:hover {
  text-decoration: underline;
  color: #fff;
}
.header__info-text {
  color: #fff;
  text-align: center;
}
.header__box-heading {
  line-height: 1.2;
  color: #edf2ff;
  font-size: clamp(2.2rem, 0.75rem + 8.2857vw, 8rem);
}
.header__box-heading span {
  display: inline-block;
  position: relative;
  width: max-content;
  padding: 0.3em 0.2em;
  z-index: 1;
}
.header__box-heading span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  animation: xd 1s forwards;
  height: 60%;
  transform: translate(0, -50%);
  border-radius: 17px;
  background-color: #fdb802;
  z-index: -1;
}
.header__box-text {
  font-size: clamp(1.6rem, 1.3rem + 1.7143vw, 2.8rem);
  color: #dcdcdc;
  font-weight: 200;
}

@keyframes xd {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .header__box {
    text-align: center;
  }
  .header__box-heading span {
    transform: rotate(-3deg);
  }
  .header__buttons {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .header {
    background-image: url("../../dist/img/header_big.png");
  }
}