@use "./base/colors" as *;
@use "./base/reset" as *;
@use "./layout/header" as *;
@use "./layout/navbar" as *;
@use "./abstracts/mixins" as *;
@use "./pages/services" as *;
@use "./pages/about" as *;
@use "./pages/process" as *;
@use "./pages/faq" as *;
@use "./pages/quote" as *;
@use "./pages/contact" as *;
@use "./layout/footer" as *;

@media (min-width: 768px) {
	.header {
		&__box {
			text-align: center;
			&-heading {
				& span {
					transform: rotate(-3deg);
				}
			}
		}
		&__buttons {
			flex-direction: row;
		}
	}
}
@media (min-width: 768px) {
	.header {
		background-image: url("../../dist/img/header_big.png");
	}
}
